.card {
  width: 750px;
}

.card.blue-card {
  background-color: #1e3684;
  color: #ffffff;
}

.card.brown-card {
  background-color: #b7a088;
  color: #ffffff;
}

.card .content {
  flex: auto;
}

.card .content.content-centered {
  padding-top: 210px;
  padding-bottom: 210px;
}

.card .content.content-centered > * {
  margin-left: auto !important;
  margin-right: auto !important;
  width: 450px;
}

.card .content.content-centered .title {
  line-height: 1.3;
  font-size: 36px;
}

.card .content.content-centered .title-secondary:not(.preserve-color) {
  color: rgba(255, 255, 255, 0.7);
}

.btn {
  height: 40px;
  margin: 0;
  border: none;
  overflow: visible;
  font: inherit;
  color: inherit;
  display: inline-block;
  box-sizing: border-box;
  padding: 0 25px;
  vertical-align: middle;
  font-size: 14px;
  line-height: 38px;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  letter-spacing: 0;
  border-radius: 500px;
}

.btn:not(:disabled) {
  cursor: pointer;
}

.btn.btn-primary {
  color: #ffffff;
}

.btn.btn-secondary {
  color: #ffffff;
}

.navbar {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: center;
}

.navbar > * {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  min-height: 100px;
  padding: 0 20px;
}

.navbar > .navbar-left {
  margin-right: auto;
}

.section.section-secondary {
  background: #1e3684;
}

